#hometopCarousel {
  .carousel-indicators {
    bottom: 2%;
    justify-content: flex-start;
    margin-left: 9%;

    li {
      border-radius: 50%;
      height: 15px;
      width: 15px;
    }
  }

  .discover-btn {
    position: absolute;
    top: 80%;
    z-index: 1;
    left: 8%;
  }
}

.stick-heading {
  font-size: 8rem;
  text-align: center;
  font-family: "SignPainter", cursive;
  color: #efeadf;
  line-height: 1;
  margin: 20px 0 0 0;
  width: 100%;
}

.discover-btn {
  color: $gold;
  border-color: $gold;
  border-radius: 30px;
  padding: 0.4rem 1.2rem;

  &:hover {
    background-color: $gold;
    color: white;
  }
}

#sticksCarousel .carousel-item.active,
#sticksCarousel .carousel-item.carousel-item-start {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  .stick__image {
    img {
      transform: scale(1);
    }
  }
}

.stick__image {
  img {
    width: 100%;
    max-width: 700px;
    transform: scale(0);
    transition: all 0.3s;
  }
}

#sticksCarousel {
  .carousel-control-prev {
    display: none;
  }
}

.stick__content {
  width: 50%;
}

.stick__image {
  width: 50%;
}

.fellow-ice {
  padding: 60px 15px;
  background: #eff3f8;
}

.fellow-ice .container {
  display: flex;
  align-items: center;
}

.fellow-content {
  padding: 0 50px;
}

.fellow-content h3 {
  font-weight: bold;
}

.receipe-heading {
  font-size: 11rem;
  text-align: center;
  font-family: "SignPainter", cursive;
  color: rgba(149, 63, 31, 0.37);
  line-height: 1;
  margin: 20px 0 0 0;
  width: 100%;
  position: relative;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
  }
}

.recipe-card__img img {
  width: 100%;
}

.recipe-card__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;

  .recipe-name {
    font-weight: bold;
    color: $gold;
  }

  .options em {
    margin: 0 0.3rem;
    font-size: 18px;
    color: #a1a1a1;
  }
}

#videoCarousel {
  .carousel-inner {
    position: relative;
  }

  .playbutton {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);

    svg {
      width: 150px;
      height: 150px;
    }
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    color: $gold;
    background-image: none;
    border: 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  }

  .carousel-control-prev,
  .carousel-control-next {
    top: auto;
    bottom: 50px;
    width: 60px;
    left: auto;
  }

  .carousel-control-prev {
    right: 50px;
  }
}

.video-desc {
  display: flex;
  margin: 2rem 0;
}

.video-desc em {
  display: inline-block;
  margin-right: 1rem;
  transform: rotate(90deg);
}

.video-detail {
  display: flex;
  flex-direction: column;
}

.community-heading {
  font-size: 8rem;
  text-align: center;
  font-family: "SignPainter", cursive;
  color: #efeadf;
  line-height: 1;
  margin: 20px 0 0 0;
  width: 100%;
}
