.story-heading {
  font-size: 20px;
  font-weight: bold;
  color: $gold-light;
}

.nav-pills .nav-link {
  border-radius: 0;
  color: #000;
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: transparent;
  color: $gold-light;
  border-bottom: 1px solid $gold-light;
}

.story-desc {
  line-height: 2;
}

.tab-content > .active {
  display: flex;
  justify-content: space-between;
}

.tab-content .content {
  width: 68%;
  padding-left: 30px;
}

.tab-content .thumb {
  width: 30%;
}

.tab-content .thumb img {
  width: 100%;
}

.story-main {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+78,00193e+78,00193e+100 */
  background: rgb(255, 255, 255); /* Old browsers */
  background: -moz-linear-gradient(
    left,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 1) 78%,
    rgba(0, 25, 62, 1) 78%,
    rgba(0, 25, 62, 1) 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 1) 78%,
    rgba(0, 25, 62, 1) 78%,
    rgba(0, 25, 62, 1) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 1) 78%,
    rgba(0, 25, 62, 1) 78%,
    rgba(0, 25, 62, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00193e',GradientType=1 ); /* IE6-9 */

  .container {
    max-width: unset;
    width: 77%;
  }
}
