.footer {
  background: #00193e;
  color: #d8d8d8;
  padding-top: 70px;
  font-size: 15px;
  line-height: 1.7;
}

.footer a {
  display: block;
  color: #d8d8d8;
  text-decoration: none;
}

.social-block {
  display: flex;
  font-size: 20px;
  line-height: 1;

  a {
    color: $gold;
    margin: 0 10px;
  }
}

.btn-subscribe {
  border-radius: 50%;
  background-color: $gold;
  color: #fff;
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 20px;
}

.subscribe-input-group input {
  width: 40%;
  margin-right: 10px;
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid #fff;
  color: #fff;
  flex: 1;

  &::placeholder {
    color: #fff;
  }
}

.subscribe-group {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
}

.subscribe-input-group {
  display: flex;
  max-width: 400px;
  flex: 1;
  margin-left: 15px;
}

.footer-link-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.social-col {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-link {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
